export default function Regles({datas}){
    return (
        <>
            <h1 style={{fontFamily: 'Light', color: '#00897B', marginBottom: '50px'}}>Rgles du data {datas.type_challenge}</h1>
            <div style={{width: '100%'}}>
                <h2 style={{textAlign: 'justify', width: '90%'}}> Article n 1 : Nombre de participants par quipe </h2>
                <p style={{textAlign: 'justify', width: '90%'}}>
                    Pour participer  l'vnement, les participants devront crer des quipes contenant entre <b>{datas.nbMinParEquipe}</b> et <b>{datas.nbMaxParEquipe}</b> personnes. Toute quipe ne satisfaisant pas cette condition  la fin des inscriptions sera mise en attente jusqu' la validation de l'quipe par un gestionnaire de l'vnement.
                </p>
                {datas.regles.map((rule, index) => {
                    return (
                        <div>
                            <h2 style={{textAlign: 'justify', width: '90%'}}>Article n {(index+2)} : {rule.titre}</h2>
                            <p style={{textAlign: 'justify', width: '90%'}}>
                                {rule.contenu}
                            </p>
                        </div>
                    )
                })}
            </div>
        </>
    )
}