import '../../../style/App.css';
import '../../../fonts/Inter-Light.ttf'
import ProgressBar from '../ProgressBar';
export default function Description({datas}){

    const debutInscriptions = Date.parse(datas.date_creation);
    const debutEvenement = Date.parse(datas.date_debut);
    const finEvenement = Date.parse(datas.date_fin);

    let now = Date.now();

    let percentStageOne = ((now - debutInscriptions) / (debutEvenement - debutInscriptions))*100;
    percentStageOne = (percentStageOne > 0) ? percentStageOne : 0;
    percentStageOne = (percentStageOne <= 100) ? percentStageOne : 100;

    let percentStageTwo = 0;

    if(percentStageOne > 0){
        percentStageTwo = ((now - debutEvenement) / (finEvenement - debutEvenement))*100;
        percentStageTwo = (percentStageTwo > 0) ? percentStageTwo : 0;
        percentStageTwo = (percentStageTwo <= 100) ? percentStageTwo : 100;
    }

    let date = new Date(datas.date_debut);
    let datefin = new Date(datas.date_fin);
    now = new Date()

    return (
        <>
            <div style={{width: '100%'}}>
                <h1 style={{fontFamily: 'Light', color: '#00897B', marginBottom: '50px'}}>Phases</h1>
                <ProgressBar bgcolor={"#00897B"} completedFirstStep={percentStageOne} completedSecondStep={percentStageTwo} />
                <br/>
                <p style={{textAlign: 'center', width: `calc(60% + 120px)`}}>
                    Nous sommes actuellement le <b><u>{now.getDate() + "/" + (now.getMonth()+1) + "/" + now.getFullYear() + " - " + now.getHours() + "h" + ((now.getMinutes() < 10) ? "0" + now.getMinutes() : now.getMinutes())}</u></b>
                </p>
                <p style={{textAlign: 'center', width: `calc(60% + 120px)`}}>
                    Les inscriptions se terminent le <b><u>{date.getDate() + "/" + (date.getMonth()+1) + "/" + date.getFullYear() + "  " + date.getHours() + "h" + ((date.getMinutes() < 10) ? "0" + date.getMinutes() : date.getMinutes())}</u></b>
                </p>
                <p style={{textAlign: 'center', width: `calc(60% + 120px)`}}>
                    L'vnement se termine le <b><u>{datefin.getDate() + "/" + (datefin.getMonth()+1) + "/" + datefin.getFullYear() + "  " + datefin.getHours() + "h" + ((datefin.getMinutes() < 10) ? "0" + datefin.getMinutes() : datefin.getMinutes())}</u></b>
                </p>
                <h1 style={{fontFamily: 'Light', color: '#00897B', marginBottom: '50px', marginTop: '70px'}}>Qu'est ce qu'un{datas.type_challenge === "Data Battle" ? "e" : null} {datas.type_challenge} IA-Pau ?</h1>
                <p style={{fontFamily: 'Light', fontSize: '17px', textAlign: 'justify', width: '90%'}}>{
                    (datas.type_challenge === "Data Battle") 
                    ?   "Une data battle dure un mois. Chaque semaine, les quipes sont amenes  rpondre  un questionnaire de 4-5 questions  rponses ouvertes. Pour chaque rponse, une \"note\" sera donne donnant lieu  un classement hebdomadaire des quipes lice. A la fin, chaque quipe prsente son travail devant un jury constitu de membres de l'association et d'experts."
                    :   "Un data challenge dure entre deux jours et un mois. Au dbut, des entreprises proposent des sujets sur lesquels peuvent s'inscrire les quipes. A la fin de l'vnement, les projets de chaque quipes sont valus afin de slectionner les finalistes qui prsenteront leurs projets devant des jury constitus de membres de l'association et d'experts dans le but d'en dgager un podium ainsi qu'un vainqueur !"
                }</p>
                {
                    datas.description && datas.description !== "" && (
                        <>
                            <h1 style={{fontFamily: 'Light', color: '#00897B', marginBottom: '50px', marginTop: '70px'}}>Description de l'vnement</h1>
                            <p style={{fontFamily: 'Light', fontSize: '17px', textAlign: 'justify', width: '90%'}}>
                                {datas.description}
                            </p>
                        </>
                    )
                }
            </div>
        </>
    )
}